*{
  padding:0;
  margin:0;
  box-sizing:border-box;
}
.contain{
  width:100vw;
  height:100vh;
  display:grid;
  place-items:center;
  position:relative;

}

.containColor{
  width:95%;
  height:95%;
  position:absolute;
 
  display: grid;
  place-items: center;
}

.name{
  font-size:5vmin;
  font-weight:bold;
 
}

button{
  position: absolute;
  width:15vmin;
  height:5vmin;
  left:45%;
  bottom:5%;
  font-size: 3vmin;
  font-weight: bold;
  outline:none;
  border:none;;
}